﻿
.js-enabled .app-js-show {
    display: block;
}

.app-js-show {
    display: none;
}

.app-button-link {
    border: none;
    padding: 0 !important;
    color: $govuk-link-colour;
    cursor: pointer;
    background: none !important;

    &:hover {
        text-decoration: none;
    }

    &:focus {
        background-color: $govuk-focus-colour !important;
    }
}

/*todo: we've copied the prototype for the header html & css, but it's not ideal.
    the prototype doesn't match the ideal design. i'll add a story to match the ideal design
    e.g.
    * bar '|' lives on its own. should it belong to the product name?
      we could implement this by having a bar to the left of the 2 span elements and effectively hiding it on the first
    see: https://stackoverflow.com/questions/45309860/flexbox-target-wrapped-items
    * product name within govuk-header__logo div
*/

.app-header {
    background-color: #003a69;
    border-bottom: 10px solid #347ca9;

    .govuk-header__container {
        @include govuk-responsive-padding(5, "top");
        @include govuk-responsive-padding(3, "bottom");
    }

    .govuk-header__logo {
        width: 100%;
    }

    .app-header-logo-link:hover, .app-header-logo-link:active {
        /* govuk-header__link--homepage uses a border rather than an underline, so we can't do this... */
        /*   text-underline-offset: 10px;*/
        /*font-weight the equivalent like this..*/
        padding-bottom: 7px;
    }

    .govuk-header__logotype-crown-fallback-image {
        height: 45px;
        width: 45px;
        float: left;
        @include govuk-responsive-margin(2, "right");
    }

    .govuk-header__logotype-text, .govuk-header__product-name {
        line-height: 2.5rem !important;
        font-size: 1.2rem;
    }
}

@import "pages/ServiceFilter";
